body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --body-large: "Plus Jakarta Sans";
  --font-font-awesome-5-free: "Font Awesome 5 Free";
  --font-open-sans: "Open Sans";

  /* font sizes */
  --font-size-21xl: 40px;
  --body-small-size: 14px;
  --body-large-size: 18px;
  --body-medium-size: 16px;
  --font-size-17xl: 36px;
  --h4-size: 24px;
  --font-size-185xl: 204px;
  --h3-size: 32px;

  /* Colors */
  --dark-green: #20484f;
  --color-darkslategray-100: #2d3142;
  --border: rgba(32, 72, 79, 0.1);
  --color-tomato-100: #f44335;
  --color-gainsboro: #ddd;
  --white: #fff;
  --color-gray-100: #fafafa;
  --dark: #072125;
  --color-gray-200: #131313;
  --color-black: #000;
  --color-whitesmoke-100: #f4f2f2;
  --color-slategray: #757095;
  --color-springgreen-100: #01ff85;
  --color-springgreen-200: #00ff84;

  /* Border radiuses */
  --br-3xs: 10px;
  --br-5xs: 8px;
  --br-1981xl: 2000px;
  --br-171xl: 190px;
  --br-base: 16px;
}
